import React from "react";
import Head from "next/head";
import { useRouter } from "next/router";
import Link from "next/link";

function AppLink({ href, children }) {
  const router = useRouter();

  const className = router.pathname === href ? "nav-item active" : "nav-item";

  return (
    <li className={className}>
      <Link href={href}>
        <a className="nav-link">{children}</a>
      </Link>
    </li>
  );
}

class MainLayout extends React.Component {
  render() {
    return (
      <div>
        <Head>
          <title>TLBB Free Tools by LingYun</title>

          <link
            rel="stylesheet"
            href="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css"
            integrity="sha384-ggOyR0iXCbMQv3Xipma34MD+dH/1fQ784/j6cY/iJTQUOhcWr7x9JvoRxT2MZw1T"
            crossOrigin="anonymous"
          />
        </Head>

        <header>
          <nav
            className="navbar navbar-expand-lg navbar-light"
            style={{ backgroundColor: "#e3f2fd" }}
          >
            <Link href="/">
              <a className="navbar-brand">TLBB Tools</a>
            </Link>
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarSupportedContent"
              aria-controls="navbarSupp`ortedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon" />
            </button>
            <div
              className="collapse navbar-collapse"
              id="navbarSupportedContent"
            >
              <ul className="navbar-nav mr-auto">
                <AppLink href="/bao-thach">Bảo Thạch</AppLink>
                <AppLink href="/nhuan-hon-thach">Nhuận Hồn Thạch</AppLink>
                {/* <AppLink href="/linh-thu-dan">Linh Thú Đan</AppLink>
                <AppLink href="/hon-bang-chau">Hồn Băng Châu</AppLink> */}
                <AppLink href="/game-server">Tra cứu tên server</AppLink>
              </ul>
            </div>
          </nav>
        </header>

        <main className="container-fluid mt-4">{this.props.children}</main>

        <footer style={{ marginTop: "4rem" }}>
          <nav className="navbar fixed-bottom navbar-light bg-light">
            <a className="navbar-brand" href="#">
              TLBB Tools by LingYun
            </a>
          </nav>
        </footer>

        <script
          src="https://code.jquery.com/jquery-3.3.1.slim.min.js"
          integrity="sha384-q8i/X+965DzO0rT7abK41JStQIAqVgRVzpbzo5smXKp4YfRvH+8abtTE1Pi6jizo"
          crossOrigin="anonymous"
        />
        <script
          src="https://cdnjs.cloudflare.com/ajax/libs/popper.js/1.14.7/umd/popper.min.js"
          integrity="sha384-UO2eT0CpHqdSJQ6hJty5KVphtPhzWj9WO1clHTMGa3JDZwrnQq4sF86dIHNDz0W1"
          crossOrigin="anonymous"
        />
        <script
          src="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/js/bootstrap.min.js"
          integrity="sha384-JjSmVgyd0p3pXB1rRibZUAYoIIy6OrQ6VrjIEaFf/nJGzIxFDsf4x0xIM+B07jRM"
          crossOrigin="anonymous"
        />

        <script async defer src="https://connect.facebook.net/en_US/sdk.js"></script>
      </div>
    );
  }
}

export default MainLayout;
